import { Component, Input, Output, signal } from '@angular/core';
import { PREMIUM_UNLIMITED_OPTIONS } from '@core/constants';
import { PremiumUnlimitedPlan } from '@core/enums';
import { ButtonType } from '@shared/components/button/button.component';
import { EventEmitter } from '@angular/core';
import { SubscriptionInfo } from '@core/models';
import { SubscriptionStatus, SubscriptionTier, SubscriptionType } from '@core/enums/profile.enum';
import { Features } from '@core/constants/features.constant';

@Component({
    selector: 'findqo-pricing-list',
    templateUrl: './pricing-list.component.html',
    styleUrls: ['./pricing-list.component.scss'],
    standalone: false
})
export class PricingListComponent {
	@Output() planClicked = new EventEmitter<string>();
	@Output() paymentPlanToggled = new EventEmitter<string>();
	@Input() subscriptionInfo: SubscriptionInfo;
	@Input() isPrivatePage: boolean;
	@Input() isLoading: boolean;
	@Input() headerTitle: string;

	paymentPlan = signal(PremiumUnlimitedPlan.MONTHLY);

	isHovered = false;

	readonly paymentPlans = [PremiumUnlimitedPlan.MONTHLY, PremiumUnlimitedPlan.YEARLY];
	readonly paymentOptions = PREMIUM_UNLIMITED_OPTIONS;
	readonly buttonType = ButtonType;
	readonly subscriptionTier = SubscriptionTier;
	readonly subscriptionStatus = SubscriptionStatus;
	readonly subscriptionType = SubscriptionType;
	readonly premiumUnlimitedPlan = PremiumUnlimitedPlan;

	readonly featuresList = Features;

	get isTrial(): boolean {
		return this.subscriptionInfo?.subscriptionType === this.subscriptionType.TRIAL;
	}

	get isActive(): boolean {
		return this.subscriptionInfo?.status === this.subscriptionStatus.ACTIVE;
	}

	get isCurrentPlan(): boolean {
		const planTierRef = this.subscriptionInfo?.planTier?.reference;
		return (
			(this.paymentPlan().valueOf() === this.premiumUnlimitedPlan.YEARLY && planTierRef === this.subscriptionTier.PREMIUM_UNLIMITED_YEARLY) ||
			(this.paymentPlan().valueOf() === this.premiumUnlimitedPlan.MONTHLY && planTierRef === this.subscriptionTier.PREMIUM_UNLIMITED)
		);
	}

	get isSubscriptionUpgradable(): boolean {
		return this.isTrial || !this.isActive || !this.isCurrentPlan;
	}

	onTogglePaymentPlan(): void {
		this.paymentPlan.set(this.paymentPlan() === PremiumUnlimitedPlan.MONTHLY ? PremiumUnlimitedPlan.YEARLY : PremiumUnlimitedPlan.MONTHLY);
		this.paymentPlanToggled.emit(this.paymentPlan());
	}

	onPlanClick(): void {
		this.planClicked.emit();
	}
}
