// Constants for commonly repeated values
const CANCEL_CHECK_ICONS = {
	limitedIcon: 'cancel',
	unlimitedIcon: 'check_circle',
};

const CHECK_ICONS = {
	limitedIcon: 'check_circle',
	unlimitedIcon: 'check_circle',
};

// Feature creation helper
const createFeature = ({
	title,
	limitedText,
	unlimitedText,
	limitedIcon,
	unlimitedIcon,
	toolTip,
}: {
	title: string | { desktop: string; mobile: string };
	limitedText?: string | { desktop: string; mobile: string };
	unlimitedText?: string | { desktop: string; mobile: string };
	limitedIcon?: string;
	unlimitedIcon?: string;
	toolTip: string;
}) => ({
	title: typeof title === 'string' ? { desktop: title, mobile: title } : title,
	...(limitedText && {
		limitedText: typeof limitedText === 'string' ? { desktop: limitedText, mobile: limitedText } : limitedText,
	}),
	...(unlimitedText && {
		unlimitedText: typeof unlimitedText === 'string' ? { desktop: unlimitedText, mobile: unlimitedText } : unlimitedText,
	}),
	...(limitedIcon && { limitedIcon }),
	...(unlimitedIcon && { unlimitedIcon }),
	toolTip,
	hovered: false,
});

